import '@telerik/kendo-theme-default/styles/packages/grid';
import {
    Component,
    Input,
    Output,
    ContentChildren,
    ContentChild,
    EventEmitter,
    HostBinding,
    OnDestroy,
    ViewChild,
    QueryList,
    AfterContentInit,
    OnChanges,
    SimpleChange,
    isDevMode,
    ElementRef
} from '@angular/core';

import { Subscription } from 'rxjs/Subscription';
import 'rxjs/add/operator/map';
import 'rxjs/add/operator/merge';

import { ColumnComponent } from './column.component';
import { DetailTemplateDirective } from './detail-template.directive';
import { ScrollMode } from './scrollmode';
import { SortDescriptor } from './sort-descriptor';
import { SortSettings } from './sort-settings';
import { PagerSettings } from './pager-settings';
import { isArray, isChanged } from './utils';
import { BrowserSupportService } from './browser-support.service';
import { GridDataResult, DataResultIterator, DataCollection } from './data.collection';
import { SelectionService, SelectionEvent } from './selection.service';
import { PageChangeEvent, DataStateChangeEvent } from './change-event-args.interface';
import { DetailsService } from './details.service';
import { ColumnsContainer } from './columns-container';

/**
 * Represents the Kendo UI Grid component for Angular.
 *
 * @example <caption>Basic Usage</caption>
 * ```ts-preview
 * @@Component({
 *    selector: 'my-app',
 *    template: `
 *        <kendo-grid [data]="gridData">
 *        </kendo-grid>
 *    `
 * })
 * class AppComponent {
 *    private gridData: any[] = products;
 * }
 *
 * const products = [{
 *    "ProductID": 1,
 *    "ProductName": "Chai",
 *    "UnitPrice": 18.0000,
 *    "Discontinued": true
 *  }, {
 *    "ProductID": 2,
 *    "ProductName": "Chang",
 *    "UnitPrice": 19.0000,
 *    "Discontinued": false
 *  }
 * ];
 * ```
 */
@Component({
    providers: [ BrowserSupportService, SelectionService, DetailsService ],
    selector: 'kendo-grid',
    template: `
        <ng-content select="kendo-grid-toolbar"></ng-content>
        <template [ngIf]="isScrollable">
            <div
                class="k-grid-header"
                [style.paddingRight.px]="scrollbarWidth">
                <div class="k-grid-header-locked"
                    *ngIf="lockedColumns.length"
                     [style.width.px]="lockedWidth">
                    <table>
                        <colgroup kendoGridColGroup
                            [columns]="lockedColumns"
                            [detailTemplate]="detailTemplate">
                        </colgroup>
                        <thead kendoGridHeader
                            [scrollable]="true"
                            [columns]="lockedColumns"
                            [sort]="sort"
                            [sortable]="sortable"
                            (sortChange)="sortChange.emit($event)"
                            [detailTemplate]="detailTemplate">
                        </thead>
                    </table>
                </div><div class="k-grid-header-wrap" #header
                    [kendoGridResizableContainer]="lockedColumns.length"
                    [lockedWidth]="lockedWidth + scrollbarWidth + 3">
                    <table [style.width.px]="nonLockedWidth">
                        <colgroup kendoGridColGroup
                            [columns]="nonLockedColumns"
                            [detailTemplate]="detailTemplate">
                        </colgroup>
                        <thead kendoGridHeader
                            [scrollable]="true"
                            [columns]="nonLockedColumns"
                            [sort]="sort"
                            [sortable]="sortable"
                            (sortChange)="sortChange.emit($event)"
                            [detailTemplate]="detailTemplate">
                        </thead>
                    </table>
                </div>
            </div>
            <kendo-grid-list
                [data]="view"
                [height]="height"
                [rowHeight]="rowHeight"
                [detailRowHeight]="detailRowHeight"
                [total]="isVirtual ? view.total : pageSize"
                [take]="pageSize"
                [skip]="skip"
                [columns]="columnsContainer"
                [selectable]="selectable"
                [detailTemplate]="detailTemplate"
                (containerScroll)="containerScroll($event)"
                (pageChange)="notifyPageChange('list', $event)">
            </kendo-grid-list>
            <div
                *ngIf="showFooter"
                class="k-grid-footer"
                [style.paddingRight.px]="scrollbarWidth">
                <div
                    *ngIf="lockedColumns.length"
                    class="k-grid-footer-locked"
                    [style.width.px]="lockedWidth">
                    <table>
                        <colgroup kendoGridColGroup
                            [columns]="lockedColumns"
                            [detailTemplate]="detailTemplate">
                        </colgroup>
                        <tfoot kendoGridFooter
                            [scrollable]="true"
                            [columns]="lockedColumns"
                            [detailTemplate]="detailTemplate">
                        </tfoot>
                    </table>
                </div><div #footer
                    class="k-grid-footer-wrap"
                    [kendoGridResizableContainer]="lockedColumns.length"
                    [lockedWidth]="lockedWidth + scrollbarWidth + 3">
                    <table>
                        <colgroup kendoGridColGroup
                            [columns]="nonLockedColumns"
                            [detailTemplate]="detailTemplate">
                        </colgroup>
                        <tfoot kendoGridFooter
                            [scrollable]="true"
                            [columns]="nonLockedColumns"
                            [detailTemplate]="detailTemplate">
                        </tfoot>
                    </table>
                </div>
            </div>
        </template>
        <template [ngIf]="!isScrollable">
            <table>
                <colgroup kendoGridColGroup
                    [columns]="columns"
                    [detailTemplate]="detailTemplate">
                </colgroup>
                <thead kendoGridHeader
                    [scrollable]="false"
                    [columns]="columns"
                    [sort]="sort"
                    [sortable]="sortable"
                    (sortChange)="sortChange.emit($event)"
                    [detailTemplate]="detailTemplate">
                </thead>
                <tbody kendoGridTableBody
                    [data]="view"
                    [skip]="skip"
                    [columns]="columns"
                    [selectable]="selectable"
                    [detailTemplate]="detailTemplate">
                </tbody>
                <tfoot kendoGridFooter
                    *ngIf="showFooter"
                    [scrollable]="false"
                    [columns]="columns"
                    [detailTemplate]="detailTemplate">
                </tfoot>
            </table>
        </template>
        <kendo-pager
            *ngIf="!isVirtual && pageable"
            [pageSize]="pageSize"
            [total]="view.total"
            [skip]="skip"
            [options]="pageable"
            (pageChange)="notifyPageChange('pager', $event)">
        </kendo-pager>
    `
})
export class GridComponent implements AfterContentInit, OnDestroy, OnChanges {
    /**
     * Sets the data of the Grid. If an array is provided, the Grid automatically gets the total count.
     */
    @Input() public data: Array<any> | GridDataResult = [];

    /**
     * Defines the page size used by the Grid pager.
     * It is required by the [paging functionality]({% slug overview_grid_kendouiforangular %}#paging).
     */
    @Input() public pageSize: number;

    /**
     * Defines the height (in pixels) that is used when the `scrollable` option of the Grid is set.
     */
    @Input() public height: number;

    /**
     * Defines the row height that is used when the `scrollable` option of the Grid is set to `virtual`.
     * It is required by the [virtual scrolling functionality]({% slug overview_grid_kendouiforangular %}#virtual-scrolling).
     */
    @Input() public rowHeight: number;

    /**
     * Defines the detail row height that is used when the `scrollable` option of the Grid is set to `virtual`.
     * It is required by the [virtual scrolling functionality]({% slug overview_grid_kendouiforangular %}#virtual-scrolling).
     */
    @Input() public detailRowHeight: number;

    /**
     * Defines the number of records to be skipped by the pager.
     * It is required by the [paging functionality]({% slug overview_grid_kendouiforangular %}#paging).
     */
    @Input() public skip: number = 0;

    /**
     * Defines the scroll mode used by the Grid. The available options are:
     *  - `none`&mdash;Renders no scrollbar.
     *  - `scrollable`&mdash;This is the default scroll mode. It requires the setting of the `height` option.
     *  - `virtual`&mdash;Displays no pager and renders a portion of data (optimized rendering) while the user is scrolling the content.
     */
    @Input() public scrollable: ScrollMode = 'scrollable';

    /**
     * Enables the single-row [selection]({% slug overview_grid_kendouiforangular %}#selection) of the Grid.
     */
    @Input() public selectable: boolean = false;

    /**
     * Sorts the descriptors by which the data has to be sorted.
     */
    @Input() public set sort(value: Array<SortDescriptor>) {
        if (isArray(value)) {
            this._sort = value;
        }
    };

    public get sort(): Array<SortDescriptor> {
        return this._sort;
    };

    /**
     * Enables the [sorting]({% slug overview_grid_kendouiforangular %}#sorting) of the Grid columns that have their `field` option set.
     */
    @Input() public sortable: SortSettings = false;

    /**
     * Configures the pager of the Grid. The available options of the `PagerSettings` are:
     *
     * - `buttonCount: Number`&mdash;Sets the maximum numeric buttons count before the buttons are collapsed.
     * - `info: Boolean`&mdash;Toggles the information about the current page and the total number of records.
     * - `type: PagerType`&mdash;Accepts the `numeric` (buttons with numbers) and `input` (input for typing the page number) values.
     * - `pageSizes: Boolean` or `Array<number>`&mdash;Shows a menu for selecting the page size.
     * - `previousNext: Boolean`&mdash;Toggles the **Previous** and **Next** buttons.
     */
    @Input() public pageable: PagerSettings | boolean = false;

    /**
     * The `pageChange` event is trigged when the page of the Grid is changed. You have to handle the event yourself and sort the data.
     */
    @Output() public pageChange: EventEmitter<PageChangeEvent> = new EventEmitter<PageChangeEvent>();

    /**
     * The `sortChange` event is trigged when the sorting of the Grid is changed. You have to handle the event yourself and sort the data.
     */
    @Output() public sortChange: EventEmitter<Array<SortDescriptor>> = new EventEmitter<Array<SortDescriptor>>();

    /**
     * The `selectionChange` event is trigged when the user selects a Grid row.
     */
    @Output() public selectionChange: EventEmitter<SelectionEvent> = new EventEmitter<SelectionEvent>();

    /**
     * The `dataStateChange` event is trigged when the data state of the Grid is changed.
     */
    @Output() public dataStateChange: EventEmitter<DataStateChangeEvent> = new EventEmitter<DataStateChangeEvent>();

    @ContentChildren(ColumnComponent) public columns: QueryList<ColumnComponent> = new QueryList<ColumnComponent>();

    @HostBinding('class') get className(): string {
        return [
            'k-widget',
            'k-grid',
            this.lockedColumns.length && 'k-grid-lockedcolumns',
            this.isVirtual && 'k-grid-virtual'
        ].filter(Boolean).join(' ');
    }

    @ContentChild(DetailTemplateDirective) public detailTemplate: DetailTemplateDirective;

    @ViewChild("header") public header: any;
    @ViewChild("footer") public footer: any;

    private scrollbarWidth: number;

    private columnsContainer: ColumnsContainer = new ColumnsContainer(() => this.columns);
    private shouldGenerateColumns: boolean = true;
    private view: DataCollection = new DataCollection(() => new DataResultIterator(this.data));

    public get showFooter(): boolean {
        return this.columns.reduce((acc, column) => column.footerTemplateRef ? acc + 1 : acc, 0) > 0;
    }

    private _sort: Array<SortDescriptor> = new Array<SortDescriptor>();

    private get isVirtual(): boolean {
        return this.scrollable === 'virtual';
    }

    private get isScrollable(): boolean {
        return this.scrollable !== 'none';
    }

    public get lockedColumns(): QueryList<ColumnComponent> {
        return this.columnsContainer.lockedColumns;
    }

    public get nonLockedColumns(): QueryList<ColumnComponent> {
        return this.columnsContainer.nonLockedColumns;
    }

    private get lockedWidth(): number {
        return this.lockedColumns.reduce((prev, curr) => prev + (curr.width || 0), 0);
    }

    private get nonLockedWidth(): number {
        if (this.lockedColumns.length) {
            return this.nonLockedColumns.reduce((prev, curr) => prev + (curr.width || 0), 0);
        }
        return undefined;
    }

    private selectionSubscription: Subscription;
    private stateChangeSubscription: Subscription;

    constructor(supportService: BrowserSupportService,
                private selectionService: SelectionService,
                public wrapper: ElementRef) {
        this.scrollbarWidth = supportService.scrollbarWidth;

        this.selectionSubscription = selectionService.changes.subscribe((event: SelectionEvent) => {
            this.selectionChange.emit(event);
        });

        this.attachStateChangesEmitter();
    }

    public ngOnChanges(changes: {[propertyName: string]: SimpleChange}): void {
        if (isChanged("data", changes)) {
            this.autoGenerateColumns();
        }
    }

    public ngAfterContentChecked(): void {
        this.columnsContainer.refresh();
        this.verifySettings();
    }

    public ngAfterContentInit(): void {
        this.shouldGenerateColumns = !this.columns.length;
        this.autoGenerateColumns();

        this.columns.changes.subscribe(() => this.verifySettings());
    }

    public ngOnDestroy(): void {
        if (this.selectionSubscription) {
            this.selectionSubscription.unsubscribe();
        }

        if (this.stateChangeSubscription) {
            this.stateChangeSubscription.unsubscribe();
        }
    }

    protected containerScroll({ scrollLeft }: { scrollTop: number, scrollLeft: number }): void {
        this.header.nativeElement.scrollLeft = scrollLeft;
        if (this.footer) {
            this.footer.nativeElement.scrollLeft = scrollLeft;
        }
    }

    protected notifyPageChange(source: string, event: any): void {
        if (source === "list" && !this.isVirtual) {
            return;
        }

        this.pageChange.emit(event);
    }

    private verifySettings(): void {
        if (isDevMode()) {
            if (this.lockedColumns.length && this.detailTemplate) {
                throw new Error('Having both detail template and locked columns is not supported');
            }

            if (this.lockedColumns.length && !this.nonLockedColumns.length) {
                throw new Error('There should be at least one non locked column');
            }

            if (this.lockedColumns.length && this.columns.filter(x => !x.width).length) {
                throw new Error('Locked columns feature requires all columns to have width set');
            }

            if (this.lockedColumns.length && !this.isScrollable) {
                throw new Error('Locked columns are only supported when scrolling is enabled');
            }
        }
    }

    private autoGenerateColumns(): void {
        if (this.shouldGenerateColumns && !this.columns.length && this.view.length) {
            this.columns.reset(Object.keys(this.view.first).map(x => new ColumnComponent(x)));
        }
    }

    private attachStateChangesEmitter(): void {
        this.stateChangeSubscription =
            this.pageChange.map(x => ({ skip: x.skip, sort: this.sort, take: x.take }))
            .merge(this.sortChange.map(sort => ({ skip: this.skip, sort: sort, take: this.pageSize })))
            .subscribe(x => this.dataStateChange.emit(x));
    }
}



